<template>
  <div>
    <div class="input-group mb-3">
      <div class="input-group-prepend">
        <datetime
          type="datetime"
          v-model="start_date"
          placeholder="Select date"
          input-class="form-control"
          value-zone="Utc"
          :format="{
            year: 'numeric',
            month: 'long',
            day: 'numeric',
            hour: 'numeric',
            minute: '2-digit',
            timeZoneName: 'short'
          }"
          :phrases="{ ok: 'Continue', cancel: 'Exit' }"
          :hour-step="1"
          :minute-step="5"
          :min-datetime="minDatetime"
          :max-datetime="maxDatetime"
          :week-start="7"
          auto
        ></datetime>
      </div>
      <span class="input-group-text">-></span>
      <datetime
        type="datetime"
        v-model="end_date"
        placeholder="Select date"
        input-class="form-control"
        value-zone="Utc"
        :format="{
          year: 'numeric',
          month: 'long',
          day: 'numeric',
          hour: 'numeric',
          minute: '2-digit',
          timeZoneName: 'short'
        }"
        :phrases="{ ok: 'Continue', cancel: 'Exit' }"
        :hour-step="1"
        :minute-step="5"
        :min-datetime="minDatetime"
        :max-datetime="maxDatetime"
        :week-start="7"
        auto
      ></datetime>
      <input type="hidden" :id="sname" :name="sname" :value="start_date" />
      <input type="hidden" :id="ename" :name="ename" :value="end_date" />
    </div>
    <!-- UTC: {{ start_date }} UTC: {{ end_date }} -->
  </div>
</template>
<script>
export default {
  data() {
    return {
      start_date: "",
      end_date: ""
    };
  },
  props: {
    start: {
      type: String
    },
    end: {
      type: String
    },
    prefix: {
      type: String,
      default: null
    },
    minDatetime: {
      type: String,
      default: null
    },
    maxDatetime: {
      type: String,
      default: null
    }
  },
  computed: {
    sname: function() {
      return this.prefix + "_start_date";
    },
    ename: function() {
      return this.prefix + "_end_date";
    }
  },
  mounted: function() {
    this.$nextTick(function() {
      this.start_date = this.start;
      this.end_date = this.end;
      // entire view has been rendered
    });
  }
};
</script>
