import Vue from "vue";
import { Datetime } from "vue-datetime";
import DateRange from "./components/DateRange.vue";
import DateSelect from "./components/DateSelect.vue";
// import EventEdit from "./EventEdit.vue";
import MenuEdit from "./components/MenuEdit.vue";
import VueNumeric from "vue-numeric";

Vue.component("datetime", Datetime);
Vue.component("daterange", DateRange);
Vue.component("dateselect", DateSelect);
Vue.config.productionTip = false;
import "vue-datetime/dist/vue-datetime.css";

import vueCustomElement from "vue-custom-element";
Vue.use(vueCustomElement);

// as <date-select></date-select>
Vue.customElement("date-select", DateSelect);
Vue.customElement("date-range", DateRange);
Vue.customElement("menu-edit", MenuEdit);
Vue.customElement("vue-numeric", VueNumeric);

// as <div id="date-select"></div>
new Vue({
  render: h => h(DateSelect)
}).$mount("#date-select");
new Vue({
  render: h => h(DateRange)
}).$mount("#date-range");
new Vue({
  render: h => h(MenuEdit)
}).$mount("#menu-edit");
new Vue({
  render: h => h(VueNumeric)
}).$mount("#vue-numeric");
