<template>
  <div>
    <VueNestable v-model="nestableItems">
      <VueNestableHandle
        slot-scope="{ item }"
        :item="item"
        v-on:click.native="item_click(item)"
        :class="{ active: item == active_item }"
      >
        <i :class="item.handleicon" v-if="item.handleicon" />
        {{ item.text }}
      </VueNestableHandle>
    </VueNestable>
    <linkedit :link="active_item"></linkedit>
  </div>
</template>

<script>
import { linkedit } from "./LinkEdit.vue";
import { VueNestable, VueNestableHandle } from "vue-nestable";
export default {
  name: "menu-edit",
  // extends: VueNestable,
  data: function() {
    return {
      nestableItems: [],
      active_item: null
    };
  },
  props: {
    items: {},
    validate: {}
  },
  watch: {
    validate: function() {
      this.$emit("data", this.nestableItems);
      this.$emit("validated", true);
    }
  },
  mounted: function() {
    if (this.items) {
      this.nestableItems = this.items;
    }
  },
  methods: {
    item_click: function(item) {
      this.active_item = item;
    }
  },
  components: {
    linkedit,
    VueNestable,
    VueNestableHandle
  }
};
</script>
